  },
async mounted() {
 const tree = await fetch('https://dev6.automatisation.art/empower/api/departments?user_id=' + window.Telegram.WebApp.initDataUnsafe.user.id + '&user_sign=' + this.token)
 const dataTree = await tree.json();
 for (let i = 0; i <= dataTree.length; i++) {
 if(dataTree[i].parentDepartment == null) {
 const newRequest = {
 deptPicLink: dataTree[i].deptPicLink,
 name: dataTree[i].name,
