 }
 this.treeOne.push(newRequest); 
 }
 if(dataTree[i].parentDepartment != null) {
 const newRequest = {
 deptPicLink: dataTree[i].deptPicLink,
 name: dataTree[i].name,
 parentDepartment: dataTree[i].parentDepartment.name,
 transformation: false,
 child: false
 }
 this.treeTwo.push(newRequest);
 }
 }

},

  methods: {
    strelka(){
      setTimeout(async() =>{ 
        for (let i = 0; i < this.treeOne.length; i++) {
          for (let j = 0; j < this.treeTwo.length; j++) {
            if (this.treeTwo[j].parentDepartment === this.treeOne[i].name) {
              this.treeOne[i].child = true;
            }
          }
        }
        for (let i = 0; i < this.treeOne.length; i++) {
          for (let j = 0; j < this.treeTwo.length; j++) {
            if (this.team[j].department === this.treeTwo[i].name) {
              this.treeTwo[i].child = true;
            }
          }
        }
    }, 500)
    },
