    const dataTeam = await team.json();
    for (let i = 0; i <= dataTeam.length; i++) {
        if((dataTeam[i].userPicLink == null) && (dataTeam[i].role == null) && (dataTeam[i].email == null) && (dataTeam[i].department == null)) {
