            username: dataTeam[i].username,
            email: dataTeam[i].email,
            clickPhone: true
          };
          this.team.push(newRequest);
        }
        if((dataTeam[i].userPicLink != null) && (dataTeam[i].role != null) && (dataTeam[i].department != null)) {
